.trainers__title {
  font-style: normal;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  display: flex;
  align-items: center;
  letter-spacing: 0.03em;
  color: $color-black;
  font-family: "Alumni Sans", sans-serif;
  margin-top: 0;
  margin-bottom: 48px;
}

.trainers {
  width: 1160px;
  margin: 0 auto;
  padding-bottom: 140px;
}

.trainers__mini-title {
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: $color-default-white;
  margin-top: 246px;
  justify-content: center;
}

.trainers__info {
  position: absolute;
  display: none;
  padding: 54px 46px 0 45px;
  top: 0;
  left: 0;

  & h3 {
    margin: 0 auto;
    font-family: "Roboto Condensed", sans-serif;
    color: $color-default-white;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  & p {
    margin: 0 auto 30px auto;
    font-family: "Roboto Condensed", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    display: flex;
    justify-content: center;
    width: 100%;
    text-align: center;
    color: #b6bdcc;
  }
}

.trainers__list {
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  display: flex;
  flex-direction: column;
  padding-left: 26px;
  color: #ffffff;

  & li {
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    margin-bottom: 12px;
    list-style: none;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      display: inline-block;
      width: 6px;
      height: 6px;
      vertical-align: middle;
      border-radius: 50%;
      background: #FFFFFF;
      margin-right: 5px;
      left: -16px;
      top: 7px;
    }
  }
}
